'use client';

import React, { ReactNode } from "react";
import { Divider, Modal } from "antd";
import ButtonUI from "../UI/Button";

type ModalAlertProps = {
    title?: string;
    description?: string;
    icon?: ReactNode;
    onClose?: () => void;
    width?: number | string;
};

export default function ModalAlert({ title, description, icon, onClose, width }: ModalAlertProps) {
    return (  
        <Modal
            open={true}
            centered
            closable={false}
            footer={
                <div className="w-full flex justify-center">
                    <ButtonUI 
                        text="ปิด" 
                        onClick={onClose} 
                        style={{
                            backgroundColor: '#FFC124',
                            color: '#FFFFFF',
                            fontWeight: 'bold',
                            fontSize: '20px',
                            fontFamily: 'Kanit, sans-serif',
                            borderRadius: '100px',
                            height: '35px',
                            width: '170px' 
                        }}
                    />
                </div>
            }
            width={width}
            className="rounded-2xl"
        >
            <div className="flex flex-col items-center justify-center p-4">
                {icon && <div className="mb-4">{icon}</div>}
                {title && <h2 className="text-xl font-bold mb-2 text-center">{title}</h2>}
                {description && <p className="text-center text-gray-600">{description}</p>}
            </div>
        </Modal>
    )
}