'use client';

import React, { ReactNode } from "react";
import Footer from "./Footer"; 
import ButtonUI from "../UI/Button";

type PageLayoutProps = {
    children: ReactNode;
    textButton?: string;
    onClickButton?: () => void;
    styleButton?: React.CSSProperties;
};

export default function PageLayout({ children, textButton, onClickButton, styleButton }: PageLayoutProps) {
  return (
    <div className="flex min-h-screen flex-col bg-white ">
      <main className="flex flex-col flex-grow h-[calc(100vh-88px)] w-full items-center justify-center">
        {children}
      </main>
      <div className="mt-auto">
        <Footer>
          <ButtonUI style={styleButton} text={textButton} onClick={onClickButton} />
        </Footer>
      </div>
    </div>
  );
}